/***********
 * Circles *
 ***********/
.circle {
  position: absolute;
  line-height: 5em !important;
  border-radius: 50%;
  /* Circle */
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  text-align: center;
  color: #fafafa;
  font-size: 40px;
  margin: 0 auto 40px; }
  .circle.beginner {
    top: 500px;
    top: 520px;
    left: calc(50% - 100px);
    width: 200px;
    height: 200px;
    background-color: rgba(244, 67, 54, 0.5);
    border: 2px solid rgba(244, 67, 54, 0.7);
    z-index: 100; }
  .circle.proficient {
    top: 370px;
    left: calc(50% - 250px);
    width: 500px;
    height: 500px;
    background-color: rgba(255, 152, 0, 0.5);
    border: 2px solid rgba(255, 152, 0, 0.7);
    z-index: 80; }
  .circle.expert {
    left: calc(50% - 400px);
    width: 800px;
    height: 800px;
    background-color: rgba(76, 175, 80, 0.5);
    border: 2px solid rgba(76, 175, 80, 0.7);
    z-index: 60; }

